.card
    // position: relative

    transition: height .5s ease-in-out
    perspective: 100rem

    // &:hover
    &--flip
        .card__inner 
            transform: rotateY(180deg)
        
        .card, .card__back 
            // height: auto
        .card__back 
            // height: auto
            // overflow: initial
            // z-index: $z-1
            opacity: 1
            visibility: initial

        .card__front
            visibility: hidden

    &__inner
        position: relative
        transition: transform .5s ease-in-out

        transform-origin: 50% 50%
        transform-style: preserve-3d

    &__front,
    &__back,
    &__inner
        border-radius: $border-radius
        background-color: var(--light)

    &__front
        // position: relative
        // z-index: $z-1
        transition: visibility .2s cubic-bezier(.77,0,.18,1)

    &__back
        transform: rotateY(180deg)

        // height: inherit
        // overflow: hidden
        opacity: 0

        transition: height overflow opacity z-index
        transition-duration:  .5s
        transition-timing-function: ease-in-out

    &__back 
        position: absolute
        // z-index: $z-1
        top: 0
        left: 0
        visibility: hidden
        padding: 1.5rem
        
        // display: flex
        
        width: 100%
        // min-height: 100%
    

    &__img
        object-fit: cover
        border-bottom-left-radius:  $border-radius
        border-top-left-radius:  $border-radius
        

    &__items 
        padding: 1.5rem

    &__close
        position: absolute
        top: 1rem
        right: 1rem
        z-index: $z-2
        color: var(--color-danger)
        font-size: $size-up

    &__title
        font-size: $size-paragraph
        color: var(--color-1-dark)
        padding-right: .4rem

    &__location, &__amount, button
        font-size: $size-down

    &__amount
        margin: 1rem 0
        text-align: right
        span
            font-size: $size-min
        span:last-child
            font-size: $size-up
            font-weight: $fw-bold
            // color: var(--color-success-dark)

    &__btn--plus
        position: relative
        z-index: $z-1
        // transition: visibility .1s ease-in-out

    &__back
        font-size: $size-down
        
        span
            font-weight: $fw-bold
        
    hr
        color: var(--light)
    &__anulation
        text-align: center
        color: var(--color-success-dark)
        margin: .5rem 0


    button
        padding: 1rem .9rem
        font-size: 1.3rem